


























import { Component, Prop } from 'vue-property-decorator';
import { mixins } from 'vue-class-component';
import { AppModule } from '@/store/modules/app';
import { getModule } from 'vuex-module-decorators';
import Scorm from '@/mixins/Scorm';
import { Id } from '@/types/Slide';
import store from '@/store';
import Bookmark from '@/models/Bookmark';
import TableOfContentsType from '@/types/TableOfContents';
import { isSubString, isEmpty } from '@/utils/strings';

const AppState = getModule(AppModule, store);

@Component
export default class TableOfContents extends mixins(Scorm) {
  @Prop() title !: string;
  toc: TableOfContentsType = AppState.tableOfContents;
  searchInputValue: string = '';
  public created() {
    this.init();
  }

  get getAlreadyVisitedPages() {
    return AppState.alreadyVisitedPagesHash;
  }

  get getCurrentSlide(): Id {
    return AppState.getCurrentSlide;
  }

  get isLockedForward(): boolean {
    return AppState.isLockedForward;
  }

  get isTotallyLocked(): boolean {
    return AppState.isTotallyLocked;
  }

  get filteredList() {
    return this.toc.slides.filter((item)=>this.isTocSearched(item.title))
  }

  private isDisabled(id: string, index: number): boolean {
    if (this.isTocActive(id)) return false;
    if (this.isLockedForward && index > AppState.getCurrentIndex) return true;
    return !this.getAlreadyVisitedPages[id] || this.isTotallyLocked;
  }

  private setSlide(id: Id, index: number) {
    if (this.isDisabled(id, index) || id == this.getCurrentSlide) return;
    AppState.goTo([id, index]);
    let bkmrkVersion = this.getBookmark().version;
    this.setBookmark(
      new Bookmark(
        AppState.currentIndex,
        AppState.getScore,
        AppState.getTotalScore,
        AppState.getBiggestIndex,
        bkmrkVersion
      )
    );
  }

  private isTocActive(id: string): boolean {
    return id == this.getCurrentSlide;
  }

  private isTocSearched(searchText: string): boolean {
    if (isEmpty(this.searchInputValue)) return true;
    return isSubString(searchText, this.searchInputValue);
  }
}
