














































































import { Component, Vue } from 'vue-property-decorator';
import { getModule } from 'vuex-module-decorators';
import { mixins } from 'vue-class-component';
import store from '@/store';
import { AppModule } from '@/store/modules/app';
import { UserConfModule } from '@/store/modules/userConf';
import { ThemeConfModule } from '@/store/modules/theme';
import TableOfContents from '@/types/TableOfContents';
import Bookmark from '@/models/Bookmark';
import Scorm from '@/mixins/Scorm';
import Sidebar from '@/components/Sidebar.vue';
import { debounce } from '@/utils/functions';
import ThemeConf from '@/types/ThemeConf';
import { applyStyle } from '@/utils/changeStyle';
import { BTBA, ExitTrainingProperties } from '@/analytics/events/BTBA';
import Timer from 'easytimer.js';
import { AccountInfo, AccountIdentity } from '@/types/AccountInfo';
import { restoreFromLocalStorage } from '@/store/plugins/SaveUserConf';

const AppState = getModule(AppModule, store);
const UserConfState = getModule(UserConfModule, store);
const ThemeStore = getModule(ThemeConfModule, store);

@Component({ components: { Sidebar } })
export default class Training extends mixins(Scorm) {
  toc: TableOfContents = AppState.tableOfContents;
  lockBookmark: boolean = false;
  showDisclaimer: boolean = true;
  lostConnection: boolean = false;
  lostScormConnection: boolean = false;
  theme: ThemeConf | null = null;
  loadingTheme: boolean = false;

  public $refs!: {
    mainContent: HTMLFormElement;
    slideTitle: HTMLFrameElement;
    disclaimer: HTMLFormElement;
    bookmarkYes: HTMLFormElement;
    disclaimerClose: HTMLFormElement;
    modalConnection: HTMLElement;
    toogleSidebar: HTMLElement;
  };

  public created() {
    // Maybe we should re-name this to initScorm()
    // so its clear where this function comes from?
    this.init();

    this.log('BTB.conf', this.BTB_CONF);

    const resizeEnd = debounce(() => {
      // This debounce will prevent commits to vuex store until resizing is completed.
      this.handleResize();
      this.setDocHeight();
    }, 250);

    window.addEventListener('resize', resizeEnd);
    window.addEventListener('pagehide', this.beforeFinish);

    AppState.setUserId(this.getUserId());
    document.addEventListener('fullscreenchange', this.onFullscreenChange);

    // Needed to support IE11
    document.addEventListener('MSFullscreenChange', this.onFullscreenChange);

    this.applyThemeAndGoToBookmark();

    this.restoreUserConfFromLocalStorage()

    store.watch(
      () => AppState.getCurrentIndex,
      () => {
        this.updateTitle();
        this.triggerFocusOnSlideTitle();
        this.closeDisclaimer();
      }
    );
    this.updateTitle();
    AppState.updateData();

    this.triggerFocusDisclaimer();
    this.lostConnectionChecker();
  }

  mounted() {
    if (this.lostConnection) {
      this.triggerFocusOnLostConnectionsModal();
    }
    window.addEventListener('orientationchange', this.setDocHeight);
    this.setDocHeight();
    setTimeout(this.setDocHeight, 500);

    this.setupAnalytics();
  }

  beforeDestroy() {
    window.removeEventListener('orientationchange', this.setDocHeight);
  }

  public async setupAnalytics() {
    let accountInfo: any = this.getAccountInfo();
    let fromModstore = true;
    if (!accountInfo) {
      fromModstore = false;
      // Out of Modstore getting account info from package, every learner is the same user (group by company)
      try {
        let accountResult = await this.getAccountInfoFile();
        (accountInfo as any) = {...accountResult.data, external_lms: true};
      } catch (error) {
        console.log(error); // Any issues with the analitycs data, will disable analitycs tracking
        return;
      }
    }

    this.$analytics.init(accountInfo); // Init Mixpanel Integration
    this.$analytics.setAccountIdentity({
      ...(accountInfo as AccountInfo | void),
      from_modstore: fromModstore,
    } as AccountIdentity);

    this.$analytics.trackEvent(BTBA.startTraining);
    window.addEventListener('unload', this.trackClose);
    AppState.setTrainingTimer(new Timer());
    AppState.getTrainingTimer.start();
  }


  public applyThemeAndGoToBookmark() {
    if (Vue.config.productionTip) this.loadingTheme = true;
    this.theme = this.getThemeConf();

    if (this.theme && this.themeHasDefaultAttributes(this.theme)) {
      this.applyThemeHelper();
    } else {
      this.getThemeFile()
        .then(theme => {
          this.theme = theme.data;
        })
        .finally(() => {
          this.applyThemeHelper();
        });
    }
  }

  public restoreUserConfFromLocalStorage(){
    restoreFromLocalStorage((store.state as any).userConf, store.getters.storagePrefix)
  }

  public applyThemeHelper() {
    if (this.theme && this.themeHasDefaultAttributes(this.theme)) {
      ThemeStore.setTheme(this.theme);
      applyStyle(this.theme);
      if (this.theme.slides && this.theme.slides.intro)
        AppState.addIntro(this.theme.slides.intro.heading);
      if (this.theme.slides && this.theme.slides.outro)
        AppState.replaceOutro(this.theme.slides.outro.heading);
    }
    this.loadingTheme = false;
    this.goToBookmarkOrInvokePopup(this.hasSCORMBookmark());
  }

  public themeHasDefaultAttributes(theme: ThemeConf) {
    return (
      theme &&
      (theme.color_primary ||
        theme.logo ||
        theme.light_mode_allowed ||
        theme.dark_mode_allowed ||
        theme.skip_mode ||
        theme.passing_score ||
        this.hasIntroOrOutro(theme))
    );
  }

  public hasIntroOrOutro(theme: ThemeConf) {
    return theme && theme.slides && (theme.slides.intro || theme.slides.outro);
  }

  public beforeFinish() {
    let newBookmark: Bookmark = {
      ...this.getBookmark(),
      lastSlideState: AppState.getSavedData
    };
    this.setBookmark(newBookmark);
    this.finish();
  }

  lostConnectionChecker() {
    this.lostConnection = !navigator.onLine;

    setTimeout(() => {
      this.lostConnectionChecker();
    }, 1000);
  }

  public onLostConnection(error?: string) {
    if (Vue.config.productionTip && !this.lostScormConnection) {
      console.error('API Connection Lost');
      this.lostScormConnection = true;
      this.showDisclaimer = false;
      setTimeout(() => {
        this.$refs.modalConnection.focus();
      }, 300);
    }
  }

  public goToBookmarkOrInvokePopup(hasSCORMBookmark: boolean) {
    if (!hasSCORMBookmark) {
      AppState.cleanData();
      AppState.goToBookmark(this.getBookmark());
      AppState.setHasStarted(true);
    } else {
      AppState.fixScores(this.getBookmark());
      this.callBookmarkPopup(this.getBookmark().currentIndex);
    }
  }

  private setDocHeight() {
    document.documentElement.style.setProperty(
      '--vh',
      `${window.innerHeight / 100}px`
    );
  }

  private closeTraining() {
    window.close();
  }

  get showSidebar() {
    return AppState.getSideBarOpen;
  }

  get logo() {
    if (this.theme && this.theme.logo) return this.theme.logo;
    else return '';
  }

  get getCurrentIndex() {
    return AppState.currentIndex;
  }

  get getCurrentSlide() {
    return AppState.currentSlide;
  }

  get isFirst(): boolean {
    return AppState.currentSlide === this.toc.slides[0].id;
  }

  get isLast(): boolean {
    return (
      AppState.currentSlide === this.toc.slides[this.toc.slides.length - 1].id
    );
  }

  get isBackwardsDisabled(): boolean {
    return this.isFirst || this.isTotallyLocked;
  }

  get isForwardDisabled(): boolean {
    return this.isLast || this.isLockedForward;
  }

  get lightMode() {
    return UserConfState.lightMode;
  }

  get staticButtonValue() {
    return UserConfState.staticButton;
  }

  get isLockedForward(): boolean {
    return AppState.isLockedForward;
  }

  get isTotallyLocked(): boolean {
    return AppState.isTotallyLocked;
  }

  get canClose() {
    return !!window.opener;
  }

  private closeSideBar() {
    AppState.closeSideBar();
    setTimeout(() => {
      this.$refs.toogleSidebar.focus();
    }, 500);
  }

  private openSidebar() {
    AppState.openSideBar();
  }

  private updateTitle(): void {
    document.title = `${this.toc.name} - ${this.slideTitle}`;
  }

  get slideTitle(): string {
    return this.toc.slides[this.getCurrentIndex].title;
  }

  private next() {
    if (this.isLast || this.isLockedForward) return;
    AppState.increment(1);
    this.updateBookmark();
    if (this.isLast) this.setResults(AppState.getScore, AppState.getTotalScore);
    if (AppState.getIsMobile) this.closeSideBar();
  }

  private previous() {
    if (this.isFirst || this.isTotallyLocked) return;
    AppState.increment(-1);
    this.updateBookmark();
    if (AppState.getIsMobile) this.closeSideBar();
  }

  private updateBookmark() {
    let bkmrkVersion = this.getBookmark().version;
    this.setBookmark(
      new Bookmark(
        AppState.getCurrentIndex,
        AppState.getScore,
        AppState.getTotalScore,
        AppState.getBiggestIndex,
        bkmrkVersion
      )
    );
  }

  private focusOnMain(e: any) {
    e.preventDefault();
    this.triggerFocusOnMain();
  }

  private triggerFocusOnBookmarkPopup() {
    setTimeout(() => {
      if (this.$refs.bookmarkYes) this.$refs.bookmarkYes.focus();
    }, 500);
  }

  private triggerFocusDisclaimer() {
    setTimeout(() => {
      if (this.$refs.disclaimer) this.$refs.disclaimer.focus();
    }, 500);
  }

  private triggerFocusOnSlideTitle() {
    if (AppState.hasStarted) {
      setTimeout(() => {
        this.$refs.slideTitle.focus();
      }, 500);
    }
  }

  private triggerFocusOnMain() {
    setTimeout(() => {
      this.$refs.mainContent.focus();
      let firstChild = this.$refs.mainContent.children[0]
      if(firstChild){
        let secondChild = firstChild.children[0]
        if(secondChild) (secondChild as HTMLElement).focus();
      }
    }, 500);
  }

  private triggerFocusOnLostConnectionsModal() {
    this.$refs.modalConnection.focus();
  }

  private handleResize() {
    AppState.updateWindowWidth(window.innerWidth);
  }

  public goToFirstPage() {
    let bkmrk: Bookmark = this.getBookmark();
    bkmrk.currentIndex = 0;
    AppState.goToBookmark(bkmrk);
    this.lockBookmark = false;
    this.updateBookmark();
    AppState.setHasStarted(true);
  }

  public goToBookmark(bookMark: Bookmark) {
    AppState.goToBookmark(bookMark);
    this.lockBookmark = false;
    this.updateBookmark();
    AppState.setHasStarted(true);
  }

  public callBookmarkPopup(currentIndex: number) {
    if (currentIndex > 0) {
      AppState.goToBookmark(this.getBookmark());
      this.lockBookmark = true;
      AppState.setHasStarted(false);
      this.triggerFocusOnBookmarkPopup();
    }
  }

  private onFullscreenChange(e: any) {
    AppState.toggleFullscreen();
  }
  public closeDisclaimer() {
    this.showDisclaimer = false;
  }

  private trackClose() {
    this.$analytics.trackEvent(BTBA.exitTraining, {
      'Session Duration': AppState.getTrainingTimer.getTotalTimeValues()
        .seconds,
      'Success Status ': AppState.successStatus,
      'Complete Status': AppState.completionStatus,
      'Final Score': AppState.getScore,
      'Minimum Score': 0,
      'Maximum Score': AppState.getTotalScore,
      'Scaled Score': AppState.getScaledScore,
      'Is Sidebar Opened': AppState.sidebarOpen,
      'Current Page': AppState.currentSlide,
      'Page ID': AppState.getCurrentSlide,
      'Page Title': AppState.getCurrentSlideTitle
    } as ExitTrainingProperties);
  }
}
